.faqcontent {
    margin:0;
    font-family: var(--principal-font);
    font-size: 15px;
    color: var(--neutral-dark-color);
  
    background-size: cover;
  }
  

   
  
  
@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');

:root{
  --principal-font: 'Kumbh Sans', sans-serif;
  --font-size: 12px;

  --bg-gradient: linear-gradient(to bottom, hsl(273, 75%, 66%), hsl(240, 73%, 65%));

  --primary-dark-color: hsl(238, 29%, 16%);
  --primary-soft-color: hsl(14, 88%, 65%);

  --neutral-dark-color: hsl(237, 12%, 33%);
  --neutral-soft-color: hsl(240, 6%, 50%);

}




.attribution { 
  font-size: 11px; text-align: center; 
  background-color: hsl(240, 5%, 91%);
  padding: 8px 0 5px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.attribution a { color: hsl(228, 45%, 44%); }





.card-wrapper {
  min-height: 100vh;
  padding-bottom: 50px;
  padding-top: 25px;
}

p {
  line-height: 1.5;
}

h2 {
  margin: 12px 0;
}



.content-cc{
  display: flex;
  justify-content: center;
  align-items: center;
}


.faq-card {
  margin: 15px;
  
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.4),
    rgba(233, 233, 233, 0.1)
  );
  background-color: blueviolet;
  border-radius: 10px;
  border-bottom: 1px solid #ccc;
  border-radius: 20px;

  max-width: 710px;
  margin-top: 90px;
  
  box-shadow: 0px 60px 50px -25px rgba(0, 0, 0, 0.35);
}



.faq-ilustrations{
  position: relative;
}

.faq-ilustrations .mobile.ilust{
  position: absolute;
  top:-110px;
  left:-30px;
  right: 0;
}

.mobile{
  display: block;
  margin: 0 auto;
}


.faq-content {
  padding: 9px 25px 3rem; 
}

.faq-content h1 {
  font-size: 32px;
  text-align: center;
  color: #FFFFFF;
}

.faq-accordion {
  padding: 8px 0;
  border-bottom: 1px solid hsl(240, 5%, 91%);
}



input.tgg-title {
  appearance: unset;
  all:unset;
}

.faq-accordion-title label{
  display: flex;
  align-items: center;
  text-align: left;
  cursor: pointer;
}

.faq-accordion-title h2{
  font-size: 15px;
  font-weight: 400;
  text-align: left;
  color: #fff;
}

.faq-accordion-title span{
  margin-left: auto;
  transition: transform .4s ease-in-out;
}

.arrow-icon {
  color: #fff;
}



.faq-accordion-content {
  color: #e9e9e9;
  overflow:hidden;
  max-height: 0;
  text-align: left;
  transition: max-height 1s ease-in-out;
}

.faq-accordion-content p {
  transition: max-height 1s ease-in-out;
}



.faq-accordion-title:hover h2{
  color:lavender;
}

.faq-accordion .tgg-title:checked + div>label>h2 {
  font-weight: 700;
}

.faq-accordion .tgg-title:checked + div>label>span {
  will-change: transform;
  transform: rotate(180deg);
}


.faq-accordion .tgg-title:checked ~ .faq-accordion-content{
  will-change: max-height;
  max-height: fit-content;
}

.commSponsor span {
  background: 50% 100% / 50% 50% no-repeat
              radial-gradient(ellipse at bottom, #fff, transparent, transparent);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-size: 2em; 
  margin: 1em 0;
  font-weight: 700;
  animation: reveal 3000ms ease-in-out forwards 200ms,
             glow 2500ms linear infinite 2000ms;
}

@media (max-width: 600px) {
  .commSponsor span {
    font-size: 1.2em;
  }
}
